<template>
  <view class="container">
    <u-form :model="form" ref="registerForm" labelWidth="60">
      <!-- 用户名 -->
      <u-form-item prop="username">
        <u-input v-model="form.username" placeholder="请输入用户名" />
      </u-form-item>

      <!-- 密码 -->
      <u-form-item  prop="password">
        <u-input v-model="form.password" placeholder="请输入密码" password />
      </u-form-item>

      <!-- 确认密码 -->
      <u-form-item  prop="confirmPassword">
        <u-input v-model="form.confirmPassword" placeholder="请确认密码" password />
      </u-form-item>

      <!-- 上传身份证 -->
      <u-form-item label="身份证:" prop="idCard">
        <u-upload
          :fileList="form.idCard"
          @afterRead="handleIdCardUpload"
          @delete="handleIdCardDelete"
          name="6"
          multiple
          :maxCount="1"
          width="250"
          height="150"
        >
          <image
            src="https://q3.itc.cn/images01/20240507/0dfee0cd5e304702b3e63732ea9aa8ef.jpeg"
            mode="widthFix"
            style="width: 235px;height: 150px;"
          ></image>
        </u-upload>
      </u-form-item>

      <!-- 上传行驶证 -->
      <u-form-item label="行驶证:" prop="drivingLicense">
     <u-upload
       :fileList="form.drivingLicense"
       @afterRead="handleDrivingLicenseUpload"
       @delete="handleDrivingLicenseDelete"
       name="6"
       multiple
       :maxCount="1"
       width="250"
       height="150"
     >
       <image
         src="https://5b0988e595225.cdn.sohucs.com/images/20181207/2cb6c658f56849caa5ebe5c0f2b07ad5.jpeg"
         mode="widthFix"
         style="width: 235px;height: 150px;"
       ></image>
     </u-upload>
      </u-form-item>

      <!-- 提交按钮 -->
      <u-button style="margin-top: 20px;" type="primary" @click="handleRegister">注册</u-button>
    </u-form>
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        username: "", // 用户名
        password: "", // 密码
        confirmPassword: "", // 确认密码
        idCard: [], // 身份证图片
        drivingLicense: [], // 行驶证图片
      },
    };
  },
  methods: {
    // 处理身份证上传
    handleIdCardUpload(event) {
      const file = event.file;
      this.form.idCard = [{ url: file.url, name: file.name }];
    },

    // 删除身份证图片
    handleIdCardDelete() {
      this.form.idCard = [];
    },

    // 处理行驶证上传
    handleDrivingLicenseUpload(event) {
      const file = event.file;
      this.form.drivingLicense = [{ url: file.url, name: file.name }];
    },

    // 删除行驶证图片
    handleDrivingLicenseDelete() {
      this.form.drivingLicense = [];
    },

    // 处理注册
    handleRegister() {
      if (!this.form.username || !this.form.password || !this.form.confirmPassword) {
        uni.showToast({
          title: "请填写完整信息",
          icon: "none",
        });
        return;
      }

      if (this.form.password !== this.form.confirmPassword) {
        uni.showToast({
          title: "两次输入的密码不一致",
          icon: "none",
        });
        return;
      }

      if (this.form.idCard.length === 0 || this.form.drivingLicense.length === 0) {
        uni.showToast({
          title: "请上传身份证和行驶证",
          icon: "none",
        });
        return;
      }

      // 模拟注册成功
      uni.showToast({
        title: "注册成功",
        icon: "success",
      });

      // 跳转到登录页面
      uni.navigateTo({
        url: "/pages/login/login",
      });
    },
  },
};
</script>


<style>
.container {
   padding:20px 40px ;
}

</style>